import Link from 'next/link';
import Layout from '../components/Layout';
import fetch from 'isomorphic-unfetch';
import Weather from '../components/Weather';

const Index = (props) => (
  <Layout weather={props.nyWeather}>
    <h1>nextJS App</h1>
    <Weather nyWeather={props.nyWeather}   />

  </Layout>
)

Index.getInitialProps = async () => {
    const response = await fetch('https://api.openweathermap.org/data/2.5/forecast?zip=11596,us&APPID=c9a0604ff76350b32823210bc6388bcb');
    const weather = await response.json()
    const nyWeather = await weather.list[weather.list.length - 1]
    console.log(nyWeather)
    return {
        nyWeather: nyWeather
    }

}

export default Index;
